import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Prodotto } from '../costruttori/Prodotto';

@Injectable({
  providedIn: 'root'
})
export class ProdottiService {

  listaProdotti: Prodotto[];

  constructor(private httpClient: HttpClient) { }

  urlListProd = 'assets/Dati/lista_prodotti.json';

  getListaProdotti(): Observable<Prodotto[]> {
   // localStorage.setItem('id_token', 'dXNlcjE6cGFzc3dvcmQx');
    return this.httpClient.get<Prodotto[]>(this.urlListProd);
  }
}
