import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { ListaProdottiComponent } from './gestione prodotti/lista-prodotti/lista-prodotti.component';
import { DettaglioProdottoComponent } from './gestione prodotti/dettaglio-prodotto/dettaglio-prodotto.component';
import { CarrelloComponent } from './carrello/carrello.component';
import { LoginAdminComponent } from './Admin/login-admin/login-admin.component';


const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'home', component: HomeComponent },
  { path: 'lista_prodotti', component: ListaProdottiComponent },
  { path: 'dettaglio_prodotti', component: DettaglioProdottoComponent },
  { path: 'carrello', component: CarrelloComponent },
  { path: 'area_riservata', component: LoginAdminComponent }


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
