import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';

import { DeviceDetectorModule } from 'ngx-device-detector';
import { HomeComponent } from './home/home.component';
import { FooterComponent } from './footer/footer.component';
import { ListaProdottiComponent } from './gestione prodotti/lista-prodotti/lista-prodotti.component';
import { DettaglioProdottoComponent } from './gestione prodotti/dettaglio-prodotto/dettaglio-prodotto.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { FormsModule } from '@angular/forms';
import { CarrelloComponent } from './carrello/carrello.component';
import { LoginAdminComponent } from './Admin/login-admin/login-admin.component';

// http client
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { AuthInterceptor } from './service/auth.interceptor';
import { AdminAreaComponent } from './Admin/admin-area/admin-area.component';

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    HomeComponent,
    FooterComponent,
    ListaProdottiComponent,
    DettaglioProdottoComponent,
    CarrelloComponent,
    LoginAdminComponent,
    AdminAreaComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    DeviceDetectorModule,
    NgbModule, // per identificare mobile o desktop
    FormsModule,
    BrowserModule, // http
    HttpClientModule // http
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
