import { Injectable } from '@angular/core';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class UtilService {

  constructor(private router: Router) { }

  public ricaricaComponent(compNome) {
    // fa un refresh del component stesso
    this.router.navigateByUrl('/RefreshComponent', { skipLocationChange: true }).then(() => {
      this.router.navigate([compNome]);
   });
  }

  public redirectToComponent(nomeComponent: string) {
    this.router.navigate([(nomeComponent)]);
  }

  public encodeBase64UsrPsw(username: string, password: string): string {
    const str = username + ':' + password;
    return btoa(str);
  }
}
